* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body,
html {
    font-size: 16px;
    font-family: 'Open Sans', sans-serif;
    background-color: rgba(51, 51, 51, 1);
    line-height: 1.4;
    color: rgba(255, 255, 255, 1);
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

body::-webkit-scrollbar {
    width: 10px;
}

body::-webkit-scrollbar-thumb {
    background-color: rgba(217, 186, 140, 1);
    border-radius: 1em;
}

body::-webkit-scrollbar-track {
    background-color: rgba(51, 51, 51, 1);
}

a {
    color: rgba(255, 255, 255, 1);
    text-decoration: none;
}

ul {
    padding: 0;
    margin: 0;
}

img {
    width: 100%;
    object-fit: cover;
}

.container {
    width: calc(100% - 260px);
    margin: 0 auto;
}

.grid-container {
    display: -ms-grid;
    display: grid;
    grid-template-areas: 'header' 'main' 'footer';
}

.content-wrapper {
    grid-area: main;
}

.footer {
    grid-area: footer;
}

.shadow-title {
    position: absolute;
    top: 15px;
    left: 50%;
    width: 100%;
    font-weight: 700;
    font-size: 55px;
    text-transform: capitalize;
    text-align: center;
    line-height: 1;
    color: rgba(255, 255, 255, 1);
    opacity: 0.04;
    transform: translate(-50%);
}

.section-header {
    text-align: center;
    margin: 0 auto 100px;
}

.section-header__title {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 50px;
    font-size: 3.125rem;
    font-style: italic;
    font-family: 'Montserrat', sans-serif;
    text-transform: capitalize;
    background: -webkit-linear-gradient(#907047, rgba(217, 186, 140, 1));
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    margin: 0 0 20px;
}

.section-header__title .lines {
    width: 200px;
    height: 1px;
    display: block;
    background: rgba(217, 186, 140, 1);
    padding: 0;
    margin: 0 20px;
}

.banner {
    min-height: 640px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 150px;
}

.banner p {
    font-size: 70px;
    font-size: 4.375rem;
    font-weight: 700;
    font-style: italic;
    color: rgba(217, 186, 140, 1);
    text-decoration: line-through;
}

