/*!********* About Banner Are Start ***************!*/

.about-banner {
    background: -webkit-gradient(linear, left top, left bottom, from(rgba(142, 139, 139, .2)), to(rgba(0, 0, 0, .7))), url("../../assets/images/gallery.jpg") no-repeat center;
    background: linear-gradient(rgba(142, 139, 139, .2), rgba(0, 0, 0, .7)), url("../../assets/images/gallery.jpg") no-repeat center;
    background-size: cover;
}

/*!********* About Banner Are End ***************!*/

/*!********* About Gallery  Area Start  ***************!*/

.general-info {
    position: relative;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    justify-content: flex-end;
}

.general-info__list {
    max-height: 610px;
    max-width: 68%;
}

.general-info__list--desc {
    height: 400px;
    position: absolute;
    right: 60%;
    top: 12%;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    justify-content: space-evenly;
    background-color: #c5a47e;
    color: var(--clr-ntrl-min);
    padding: 4.8em 3.1em;
}

.general-info__list__item h5 {
    font-size: var(--type-large);
    font-weight: var(--type-bold);
    text-align: center;
    padding-bottom: 20px;
}

.general-info__list__item p {
    font-size: var(--type-small);
    font-weight: var(--type-medium);
    text-align: center;
    padding-bottom: 10px;
}

.general-info__list__item--admissions {
    align-self: flex-end;
}

/*!********* About Gallery  Area End  ***************!*/

/*!********* About Artist  Area Start  ***************!*/

.about-artist {
    margin: 230px 0 150px;
}

.video-wrapper {
    position: relative;
}

.artist-info {
    width: 100%;
    height: 100%;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    top: 0;
    left: 0;
    background: rgba(167, 112, 45, .3);
}

.video-wrapper video {
    width: 100%;
    height: 100%;
}

.video-button-play-inner {
    cursor: pointer;
}

/*!********* About Artist  Area End  ***************!*/

/*!********* Interesting Details  Area Start  ***************!*/
.detailsList {
    width: 70%;
    margin: 0 auto 150px;
}

.detailsList_item {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    list-style-type: none;
}

.detailsList_item:nth-child(even) {
    justify-content: flex-end;
}

.detailsItem_wrapper {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    align-items: center;
    border-bottom: 1px solid var(--main-brand-color);
    padding: 50px 50px 30px 15px;
}

.detailsItem_number {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    font-size: 4em;
    padding-right: 30px;
}

.detailsItem_number-small {
    font-size: 24px;
    font-size: 1.5rem;
    padding: 10px;
}

.detailsItem_info {
    font-size: var(--type-base);
    font-weight: var(--type-medium);
    font-style: italic;
    color: #ababab;
}

/*!********* Interesting Details  Area End  ***************!*/


